import uniIcon from "@/components/uni-icon/uni-icon.vue"
import uniPopup from "@/components/uni-popup/uni-popup.vue"
import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue'
export default {
	components: {
		uniIcon,
		uniPopup,
		tkiQrcode
	},
	data() {
		return {
			couponList:[],
			val:" "
		}
	},
	onShow() {
		this.checkLogin();
		uni.showLoading({mask:true});
		this.getData();
	},
	onPullDownRefresh: function() {
		this.getData();
		uni.stopPullDownRefresh();
	},
	methods: {
		go(No,cost){
			uni.navigateTo({url: '../shop/shop?no='+No+'&cost='+cost});
		},
		showPopup(val){
			this.val = val;
			this.$refs.coupon.open();
		},
		hidePopup(){
			this.$refs.coupon.close();
		},
		getData(){
			uni.request({
				url: this.apiServer + 'merchantCashCoupon/getCashCouponRecord',
				data: {
					isEmploy:false,
					whetherToQuerySource:true
				},
				method: 'POST',
				header: {
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						let data = res.data.data;
						for(let i=0;i<data.length;i++){
							if(!data[i].merchantIoc){
								data[i].merchantIoc = '../../static/index/u207.png'
							};
						};
						this.couponList = data;
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				},
				complete: (res) => {
					uni.hideLoading();
				}
			});
		}
	}
}
